<template>
  <div class="cartcontrol">
    <transition name="move">
      <div v-if="food.count>0" class="decrease" @click.stop.prevent="decreaseCart">
        <span class="inner icon_dec"></span>
      </div>
    </transition>
    <div v-if="food.count>0" class="count">{{food.count}}</div>
    <div style="display: inline-block;padding: 6px" @click.stop.prevent="addCart($event)">
      <span class="add icon_add"></span>
    </div>
  </div>
</template>

<script>
    // @ is an alias to /src
    export default {
      props: {
        food: {
          type: Object,
        }
      },
      data() {
        return {

        };
      },
      methods:{
        addCart(event){
          if(this.food.count == null){
            this.$set(this.food,"count",1);
            this.$root.$emit("add_cart",this.food);
          }else{
            this.food.count++;
          }
          this.$store.commit({
            type:"initBallEle",
            el: event.target
          });
          this.$root.$emit("drop");
        },
        decreaseCart(event){
          if (this.food.count) {
            this.food.count--;
          }
        }
      }
    }
</script>
<style scoped lang="stylus">
  .cartcontrol
    font-size 0
    .decrease
      display inline-block
      padding 6px
      transition all 0.4s linear
      .inner
        display inline-block
        line-height 24px
        font-size 24px
        color rgb(0,160,220)
        transition all 0.5s linear
      .icon_dec
        width 24px
        height 24px
        display inline-block
        background url("../../dec.png") center center / cover
      &.move-enter,&.move-leave-active
        opacity 0
        transform translate3d(24px,0,0)
        .inner
          transform rotate(180deg)
    .count
      display inline-block
      vertical-align top
      width 12px
      padding-top 6px
      line-height 24px
      text-align center
      font-size 10px
      color rgb(147,153,159)
    .add
      display inline-block
      line-height 24px
      font-size 24px
      color rgb(0,160,220)
    .icon_add
      width 24px
      height 24px
      display inline-block
      background url("../../add.png") center center / cover
</style>
