<template>
  <transition name="move">
    <div v-show="foodShow" class="food" ref="food">
      <div class="food-content">
        <div class="top">
          <img :src="food.image">
          <span class="back" @click.stop.prevent="back"></span>
        </div>
        <div class="content">
          <p style="font-weight: bold;font-size: 14px;">{{food.name}}</p>
          <p style="color: grey;font-size: 13px;">
            <span style="margin-right: 10px;">月售{{food.sellCount}}份</span>
            <span>好评率{{food.rating}}</span>
          </p>
          <p style="margin-top: 10px;">
            <span class="now">￥{{food.price}}</span>
            <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
          </p>
          <transition name="buyFade">
            <div class="buy" v-show="!food.count || food.count===0" @click.stop.prevent="addFirst">加入购物车</div>
          </transition>
          <cartcontrol :food="food" class="buy" v-show="food.count > 0" style="background: none;bottom: 20px;"></cartcontrol>
        </div>
        <div class="info" v-show="food.info">
          <p>商品信息</p>
          <p class="text">{{food.info}}</p>
        </div>
        <div class="rating">
          <p>商品评价</p>
          <ratingselect @onlyContentChange="onlyContentChange" @typeChange="typeChange" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
          <div class="comment">
            <ul>
              <li class="rating-item" v-show="needShow(food.rating.rateType,food.rating.text)" v-for="(rating,index) in food.ratings">
                <div class="content">
                  <p>
                    <span style="color: grey;">{{rating.rateTime|format}}</span>
                    <span style="float: right;">
                      {{rating.username}}
                      <img width="12" height="12" :src="rating.avatar" style="border-radius: 50%;vertical-align: middle;">
                    </span>
                  </p>
                  <p >
                    <span v-for="item in ratings_stars[index]" :class="item == 1 ? 'small_light' : 'small_dark'"></span>
                    <span style="color: grey;" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
                  </p>
                  <p style="margin-top: 5px;">
                    <img src="../../fabulous.png" alt="" width="14" height="14" style="vertical-align: text-top" v-show="rating.rateType===0">
                    <img src="../../badpost.png" alt="" width="14" height="14" style="vertical-align: text-top" v-show="rating.rateType===1">
                    {{rating.text}}
                  </p>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
    // @ is an alias to /src
    import iscroll from "iscroll"
    import ratingselect from "../views/ratingselect.vue"
    import cartcontrol from "../views/cartcontrol.vue"
    import {formatDate} from "../common/js/formatDate"
    const ALL = 2;
    const POSITIVE = 0;
    const NEGATIVE = 1;
    export default {
      props:{
        food:{
          type:Object
        }
      },
      data() {
        return {
          foodShow:false,
          selectType: ALL,
          onlyContent: false,
          desc: {
            all: '全部',
            positive: '推荐',
            negative: '吐槽'
          },
          ratings_stars:[]
        }
      },
      methods: {
        calRatingsStars() {
          var arr;
          for (var i = 0; i < this.food.ratings.length; i++) {
            arr = [];
            for (var j = 1; j <= this.food.ratings[i].score; j++) {
              arr.push(1);//亮
            }
            for (var j = this.food.ratings[i].score; j < 5; j++) {
              arr.push(0);//暗
            }
            this.ratings_stars.push(arr);
          }
        },
        show() {
          this.foodShow = true;
          this.selectType = ALL;
          this.onlyContent = false;
          this.ratings_stars = [];
          this.$nextTick(function () {
            if (!this.scroll) {
              this.scroll = new iscroll(this.$refs.food, {
                click: true,
                mouseWheel:true,
                scrollY:true
              });
            }
            this.scroll.refresh();
            this.calRatingsStars();
          });
        },
        back() {
          this.foodShow = false;
        },
        addFirst() {
          if(this.food.count == null){
            this.$set(this.food,"count",1);
            this.$root.$emit("add_cart",this.food);
          }else {
            this.food.count++;
          }
          this.$store.commit({
            type:"initBallEle",
            el: event.target
          });
        },
        needShow(type, text) {
          if (this.foodShow === false) {
            return;
          }
          if (this.onlyContent && !text) {
            return false;
          }
          if (this.selectType === ALL) {
            return true;
          } else {
            return this.selectType === type;
          }
        },
        typeChange(type) {
          this.selectType = type;
          this.$nextTick(() => {
            this.scroll.refresh();
          })
        },
        onlyContentChange() {
          this.onlyContent = !this.onlyContent;
          this.$nextTick(() => {
            this.scroll.refresh();
          })
        }
      },
      filters: {
        format(time) {
          let date = new Date(time);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }
      },
      components:{
        ratingselect,
        cartcontrol
      }
    }
</script>
<style scoped lang="stylus">
  .food
    touch-action none
    position fixed
    top 0
    left 0
    bottom 50px
    z-index 30
    width 100%
    background #fff
    transition all 0.5s
    overflow hidden
    &.move-enter,&.move-leave-active
      transform translate3d(100%,0,0)
    .top
      position relative
      width 100%
      height 0
      padding-top 100%
      img
        position absolute
        top 0
        left 0
        width 100%
        height 100%
      .back
        width 30px
        height 30px
        position absolute
        top 10px
        left 10px
        display inline-block
        background url("../../back.png") 30px 30px / cover
    .content
      position relative
      padding 18px
      border-bottom 1px solid gainsboro
      .now
        color red
        font-size 13px
      .old
        color grey
        text-decoration line-through
        margin-left 5px
        font-size 13px
      .cartcontrol-wrapper
        position absolute
        right 12px
        bottom 12px
      .buy
        position absolute
        right 18px
        bottom 18px
        padding 0 12px
        z-index 10
        height 24px
        line-height 24px
        border-radius 12px
        font-size 10px
        color #fff
        background rgb(0,160,220)
        transition all 0.2s
        &.buyFade-leave-active,&.buyFade-enter
          opacity 0
    .info
      padding 18px
      margin-top 20px
      border-top 1px solid gainsboro
      border-bottom 1px solid gainsboro
      .text
        color gray
        font-size 13px
        line-height 20px
        text-align justify
        padding 0 10px
    .rating
      padding 18px
      margin-top 20px
      border-top 1px solid gainsboro
      .comment
        width 100%
        .rating-item
          display flex
          .content
            flex 1
            font-size 12px
            .dishes
              border 1px solid gainsboro
              color grey
              display inline-block
              height 20px
              line-height 20px
              padding 0 5px
              margin 5px
            .small_light
              height 10px
              width 10px
              display inline-block
              margin-right 5px
              background url("../../light.png") 10px 10px / cover
            .small_dark
              height 10px
              width 10px
              display inline-block
              margin-right 5px
              background url("../../dark.png") 10px 10px / cover
</style>
