<template>
  <div class="ratingselect">
    <div>
      <span @click.stop.prevent="select(2)" class="block positive" :class="{active:selectType===2}">{{desc.all}}
        <span>{{ratings.length}}</span>
      </span>
      <span @click.stop.prevent="select(0)" class="block positive" :class="{active:selectType===0}">{{desc.positive}}
        <span>{{positives.length}}</span>
      </span>
      <span @click.stop.prevent="select(1)" class="block negative" :class="{active:selectType===1}">{{desc.negative}}
        <span>{{negatives.length}}</span>
      </span>
    </div>
    <div class="switch" :class="{on:onlyContent}" @click.stop.prevent="toggleSwitch">
      <span :class="onlyContent == false ? 'gray_select' : 'green_select'"></span>
      <span>只看有内容的评价</span>
    </div>
  </div>
</template>

<script>
    // @ is an alias to /src
    import iscroll from "iscroll"
    const POSITIVE = 0;
    const NEGATIVE = 1;
    const ALL = 2;
    export default {
      props: {
        ratings: {
          type: Array,
          default() {
            return [];
          }
        },
        selectType: {
          type: Number,
          default: ALL
        },
        onlyContent:{
          type: Boolean,
          default: false
        },
        desc: {
          type: Object,
          default() {
            return {
              all: '全部',
              positive: '满意',
              negative: '不满意'
            }
          }
        }
      },
      methods: {
        select(type) {
          this.$emit('typeChange',type);
        },
        toggleSwitch() {
          this.$emit('onlyContentChange');
        }
      },
      computed: {
        positives() {
          return this.ratings.filter((rating) => {
            return rating.rateType === POSITIVE;
          });
        },
        negatives() {
          return this.ratings.filter((rating) => {
            return rating.rateType === NEGATIVE;
          });
        }
      }
    }
</script>
<style scoped lang="stylus">
  .ratingselect
    padding-top 18px
    border-bottom 1px solid rgba(7,17,27,0.1)
    .block
      display inline-block
      padding 8px 12px
      margin-right 5px
      font-size 12px
      color grey
      &.active
        color white
      &.positive
        background rgba(0,160,220,0.2)
        &.active
          background rgb(0,160,220)
      &.negative
        background rgba(77,85,93,0.2)
        &.active
          background rgba(77,85,93,0.5)
    .switch
      padding 12px 0
      color rgb(147,153,159)
      font-size 12px
      span
        height 24px
        line-height 24px
        display inline-block
      .gray_select
        width 24px
        vertical-align middle
        margin-right 5px
        background url("../../gray_select.png") 24px 24px / cover
      .green_select
        width 24px
        vertical-align middle
        margin-right 5px
        background url("../../green_select.png") 24px 24px / cover
</style>
