      methods:{
        calStar(){
          for(var i = 1;i <= this.seller.score;i++){
            this.stars.push(1);//亮
          }
          for(var i = this.seller.score;i < 5;i++){
            this.stars.push(0);//暗
          }
        },
        calRatingsStars(){
          var arr;
          for(var i = 0;i < this.ratings.length;i++){
            arr = [];
            for(var j = 1;j <= this.ratings[i].score;j++){
              arr.push(1);//亮
            }
            for(var j = this.ratings[i].score;j < 5;j++){
              arr.push(0);//暗
            }
            this.ratings_stars.push(arr);
          }
        },
        initScroll(){
          this.myscroll = new iscroll(this.$refs.ratings, {
            click: true,
            mouseWheel:true,
            scrollY:true
          });
        },
        needShow(type, text) {
          if (this.foodShow === false) {
            return;
          }
          if (this.onlyContent && !text) {
            return false;
          }
          if (this.selectType === ALL) {
            return true;
          } else {
            return this.selectType === type;
          }
        },
        typeChange(type) {
          this.selectType = type;
        },
        onlyContentChange() {
          this.onlyContent = !this.onlyContent;
        }
      },
      filters: {
        format(time) {
          let date = new Date(time);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }
      },
      created(){
        var _this = this;
//        this.$http.get("/api/sellers").then((response)=>{
//          if(response.data.errno == ERR_OK){
//            _this.seller = response.data.data;
//          }
          _this.$nextTick(() => {
            _this.calStar();
            _this.initScroll();
          })
//        }).catch((err)=>{
//          console.log(err);
//        });
//        this.$http.get("/api/ratings").then((response)=>{
//          if(response.data.errno == ERR_OK){
//            _this.ratings = response.data.data;
            _this.calRatingsStars();
//          }
//        }).catch((err)=>{
//          console.log(err);
//        });
      },
      updated(){//避免myscroll刚开始没有初始化
        this.$nextTick(() => {
          this.myscroll.refresh();
        })
      }
    }
